@import "function";

.inner {
    max-width: 1180px;
    min-width: 980px;
    margin: 0 auto;
}

.inner-1 {
    width: 1080px;
    margin: 0 auto;
}


//登录页
.login-bg {
    background-image: url(../images/mgl-login-bg.png);
    background-repeat: repeat-x;
    background-position: top center;
    background-color: #dcf0ff;
}

.login-nav {
    background: $white;
    height: $header-height + 10;
    line-height: $header-height + 10;
}

.login-cnt {
    padding-top: 150px;
    padding-bottom: 50px;
}

.login-err {
    position: absolute;
    top: -4px;
}

.login-form {
    width: 372px;
    margin-top: 40px;
    background-color: $white;
    border-radius: $baseRadius;
    box-shadow: 0 0 26px rgba(60, 155, 237, 0.2);
    margin-left: auto;
    margin-right: auto;

    .login-form-hd {
        padding-top: 20px;
        text-align: center;
        font-size: $baseFontSize + 11;
        font-weight: bold;
        color: $primary;
        line-height: 52px;
    }

    .login-wrap {
        position: relative;
    }

    .ui-form-group {
        margin-bottom: 16px;
        position: relative;

        & > i {
            position: absolute;
            font-size: 20px;
            line-height: 46px + 4px;
            left: 14px;
        }

        .ui-input-control {
            height: 46px;
            padding-left: 48px;
            font-size: 15px;
        }
    }

    .login-code {
        position: absolute;
        right: 6px;
        bottom: 0;
        line-height: 46px;
    }
}

.login-footer {
    margin-top: 14px;
    color: #999;
}


/* 系统选择界面 */
.main-fn-list {
    width: 100%;

    li {
        float: left;
        width: 12.5%;
        margin-top: 44px;
        text-align: center;

        .main-fn-tit {
            margin-bottom: 6px;
            width: 80px;
            height: 80px;
            line-height: 80px;
            margin-left: auto;
            margin-right: auto;
            text-align: center;

            img {
                border-radius: 12px;
            }
        }

        .main-fn-name {
            font-size: 15px;
        }
    }
}

/* 系统选择界面图标 */
.ico-sys,[class^="ico-sys"] {
    @include icon;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    font-size: 48px;
    color: #fff;
}

.ico-sys-crm {
    background-color: #54b1f6;
    line-height: 89px;
    &:before { 
        content: "\e8dc";
    }
}


/* 修改密码 */
.pw-layer {
    
    h2 {
        margin-top: 16px;
        margin-bottom: 6px;
    }
}
.pw-cnt {
    background-color: $white;
    width: 100%;
    min-height: 420px;
}

.pw-form {
    width: 388px;
    margin-top: 88px;
    background-color: $white;
    border-radius: $baseRadius;
    box-shadow: 0 0 26px rgba(60, 155, 237, 0.2);
    margin-left: auto;
    margin-right: auto;
}

.add-error {
    .ui-form-group {
        position: relative;
    }

    label.error {
        position: absolute;
        font-size: $baseFontSize - 1;
        margin-top: 2px;
        color: $red;
        left: 78px;
        bottom: -16px;
    }

    .radios label.error {
        bottom: -6px;
    }
}

/* 消息提示 */
.show-prompt {
    position: absolute;
    border-radius: 4px;
    padding: 20px 16px;
    background-color: rgba(0,0,0,.6);
    color: #fff;
}